export default {
	login: (state) => {
		return state.isLogin
	},
	username: (state) => {
		return state.username
	},
	goodsinfo: (state) => {
		return state.goodsinfo
	},
	news: (state) => {
		return state.news
	},
	city: (state) => {
		return state.city
	},
}


